/*************************************
Scaffolding
**************************************/
/**
 *	Set base font size to 10px
 *	This makes calculating rem values very easy
 *	Just divide your pixel value by 10
 *	Need an rem value that matches 13px? 1.3rem
 *	Need an rem value that matches 27px? 2.7rem
 */
:root {
  font-size: 10px;
}

/**
 *	Reset element margins
 */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
}

h1 + * {
  margin-top: 2rem;
}

h2 + * {
  margin-top: 2rem;
}

h3 + * {
  margin-top: 2rem;
}

h4 + * {
  margin-top: 2rem;
}

h5 + * {
  margin-top: 1.5rem;
}

h6 + * {
  margin-top: 1.5rem;
}

p + * {
  margin-top: 1.5rem;
}

ol + * {
  margin-top: 1.5rem;
}

ul + * {
  margin-top: 1.5rem;
}

* + h1 {
  margin-top: 3rem;
}

* + h2 {
  margin-top: 3rem;
}

* + h3 {
  margin-top: 3rem;
}

* + h4 {
  margin-top: 3rem;
}

* + h5 {
  margin-top: 3rem;
}

* + h6 {
  margin-top: 3rem;
}

/*
 *	Global element styling
 */
body {
  font: var(--body-font);
  color: var(--body-font-color);
}

p {
  font: var(--body-font);
  color: var(--body-font-color);
}

blockquote {
  font: var(--body-font);
  color: var(--body-font-color);
}

h1 {
  font: normal normal var(--font-weight-bold) 3.6rem/1.21 var(--font-primary);
  color: var(--h1-color);
  text-transform: uppercase;
}
@media only screen and (min-width: 1025px) {
  h1 {
    font-size: 4.8rem;
  }
}

h2 {
  font: normal normal var(--font-weight-bold) 3.6rem/1.22 var(--font-primary);
  color: var(--h2-color);
  text-transform: uppercase;
}

h3 {
  font: normal normal var(--font-weight-medium) 2.6rem/1.25 var(--font-primary);
  color: var(--h3-color);
}

h4 {
  font: normal normal var(--font-weight-medium) 1.8rem/1.44 var(--font-primary);
  color: var(--h4-color);
}

h5 {
  font: normal normal var(--font-weight-medium) 1.6rem/1.5 var(--font-primary);
  color: var(--h5-color);
}

h6 {
  font: normal normal var(--font-weight-medium) 1.2rem/1.66 var(--font-primary);
  color: var(--h6-color);
}

/**
 *	Text size modifiers - usually used with paragaph elements
 */
.large {
  font-size: calc(var(--body-font-size) + 0.2rem);
}

small,
.small {
  font-size: calc(var(--body-font-size) - 0.2rem);
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.bg-black,
.bg-blue,
.bg-red,
.block-bg-image:not(.block-bg-image--dark-text) {
  --body-font-color: var(--color-white);
  --h1-color: var(--color-white);
  --h2-color: var(--color-white);
  --h3-color: var(--color-white);
  --h4-color: var(--color-white);
  --h5-color: var(--color-white);
  --h6-color: var(--color-white);
  --button-primary-normal-text-color: var(--color-black);
  --button-primary-normal-bg-color: var(--color-white);
  --button-primary-normal-border-color: var(--color-white);
  --button-primary-disabled-bg-color: rgba(var(--color-rgb-white), 0.4);
  --button-primary-disabled-text-color: rgba(var(--color-rgb-black), 0.7);
  --button-secondary-normal-text-color: var(--color-white);
  --button-secondary-normal-border-color: var(--color-white);
  --button-secondary-disabled-text-color: rgba(var(--color-rgb-white), 0.4);
  --button-secondary-disabled-border-color: rgba(var(--color-rgb-white), 0.4);
  --form-checkmark-img: url("../../media/images/check-white.png");
  --form-radio-fill-color: var(--color-white);
}

.bg-black,
.bg-blue,
.bg-red {
  --anchor-link-normal-rgb-color: var(--color-rgb-white);
  --anchor-link-hover-rgb-color: var(--color-rgb-white);
}

.bg-black {
  background-color: var(--color-black);
}

.bg-blue {
  background-color: var(--color-blue);
}

.bg-red {
  background-color: var(--color-red);
}

.bg-gray {
  background-color: var(--color-gray);
}

.block-bg-image {
  background-color: var(--color-white);
  background-size: cover;
}
.block-bg-image.block-bg-overlay::before {
  z-index: -1;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(246, 249, 252, 0) 100%);
}
.block-bg-image.block-bg-overlay .block-inner {
  z-index: 1;
  position: relative;
}

.button,
.wysiwyg button,
input[type=submit],
.mce-button-primary {
  position: relative;
  display: inline-block;
  margin: 0;
  padding: 0.8rem 1.5rem;
  min-width: 160px;
  min-height: var(--default-input-min-height);
  border: 2px solid var(--button-primary-normal-border-color);
  font: normal normal var(--font-weight-bold) var(--body-font-size)/1.45 var(--font-primary);
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--button-primary-normal-text-color);
  background-color: var(--button-primary-normal-bg-color);
  box-shadow: var(--default-box-shadow);
  appearance: none;
  outline: none;
  cursor: pointer;
  transition: all var(--default-transition-speed) ease-in-out;
}
.button:hover, .button:active, .button.active,
.wysiwyg button:hover,
.wysiwyg button:active,
.wysiwyg button.active,
input[type=submit]:hover,
input[type=submit]:active,
input[type=submit].active,
.mce-button-primary:hover,
.mce-button-primary:active,
.mce-button-primary.active {
  color: var(--button-primary-hover-text-color);
  background-color: var(--button-primary-hover-bg-color);
  border-color: var(--button-primary-hover-border-color);
  text-decoration: none;
}
.button:disabled, .button.disabled,
.wysiwyg button:disabled,
.wysiwyg button.disabled,
input[type=submit]:disabled,
input[type=submit].disabled,
.mce-button-primary:disabled,
.mce-button-primary.disabled {
  pointer-events: none;
  cursor: not-allowed;
  color: var(--button-primary-disabled-text-color);
  background-color: var(--button-primary-disabled-bg-color);
  border-color: var(--button-primary-disabled-border-color);
}
.button.button--secondary,
.wysiwyg button.button--secondary,
input[type=submit].button--secondary,
.mce-button-primary.button--secondary {
  color: var(--button-secondary-normal-text-color);
  background-color: var(--button-secondary-normal-bg-color);
  border: 2px solid var(--button-secondary-normal-border-color);
}
.button.button--secondary:hover, .button.button--secondary:active, .button.button--secondary.active,
.wysiwyg button.button--secondary:hover,
.wysiwyg button.button--secondary:active,
.wysiwyg button.button--secondary.active,
input[type=submit].button--secondary:hover,
input[type=submit].button--secondary:active,
input[type=submit].button--secondary.active,
.mce-button-primary.button--secondary:hover,
.mce-button-primary.button--secondary:active,
.mce-button-primary.button--secondary.active {
  color: var(--button-secondary-hover-text-color);
  background-color: var(--button-secondary-hover-bg-color);
  border-color: var(--button-secondary-hover-border-color);
}
.button.button--secondary:disabled, .button.button--secondary.disabled,
.wysiwyg button.button--secondary:disabled,
.wysiwyg button.button--secondary.disabled,
input[type=submit].button--secondary:disabled,
input[type=submit].button--secondary.disabled,
.mce-button-primary.button--secondary:disabled,
.mce-button-primary.button--secondary.disabled {
  color: var(--button-secondary-disabled-text-color);
  background-color: var(--button-secondary-disabled-bg-color);
  border-color: var(--button-secondary-disabled-border-color);
}

.button--has-icon {
  min-width: 0;
  padding-left: 5.5rem;
}
.button__icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -2px;
  bottom: -2px;
  left: -2px;
  width: var(--default-input-min-height);
  background: var(--color-black);
  color: var(--color-white);
}
.button__icon {
  width: calc(var(--default-input-min-height) / 3);
  height: calc(var(--default-input-min-height) / 3);
  color: var(--color-white);
}
.button--slim {
  min-width: 125px;
  min-height: 0;
  padding: 0.4rem 1.5rem;
}

label {
  font: var(--body-font);
  color: var(--form-label-color);
}

input[type=text],
input[type=number],
input[type=tel],
input[type=url],
input[type=email],
input[type=password],
textarea,
select {
  transition: all var(--default-transition-speed) ease-out;
  appearance: none;
  border-radius: var(--default-input-border-radius);
  border: 1px solid var(--form-control-border-color-default);
  padding: var(--form-control-padding);
  background-color: var(--color-white);
  min-height: 36px;
  font: var(--body-font);
  color: var(--color-black);
  display: block;
  width: 100%;
}
input[type=text]::placeholder,
input[type=number]::placeholder,
input[type=tel]::placeholder,
input[type=url]::placeholder,
input[type=email]::placeholder,
input[type=password]::placeholder,
textarea::placeholder,
select::placeholder {
  color: var(--color-gray);
}
@media only screen and (min-width: 641px) {
  input[type=text],
input[type=number],
input[type=tel],
input[type=url],
input[type=email],
input[type=password],
textarea,
select {
    display: inline-block;
    width: auto;
  }
}
input[type=text]:focus,
input[type=number]:focus,
input[type=tel]:focus,
